import React, { useRef } from 'react'
import {Link} from 'react-router-dom'
import './css/nav.css'
import logo from './img/idly.png'

function Nav() {
    const link1 = useRef(null)
    const link2 = useRef(null)

    const onLink = (num) =>{
        link1.current.style.color = '#847F80'
        link2.current.style.color = '#847F80'
        if (num === 1) {
            link1.current.style.color = '#ff3535'            
        }else{
            link2.current.style.color = '#ff3535'
        }
    }

    return (
        <nav>
            <div id="nav-logo">
                <img className="logo" src={logo} alt="logo"/>
                <strong>IDLI</strong>
            </div>
            
            <div id="nav-links">
                <Link ref={link1} onClick={() => onLink(1)} to='/'>
                    <strong> Home </strong>
                </Link>
                <Link ref={link2} onClick={() => onLink(2)} to='/farms'>
                    <strong> Farms </strong>
                </Link>
                <a target="_blank" href="https://tronscan.org/#/address/TVzYjZWSL2drjPS8byh9soE7LUnxc1PtFY">
                    <strong> About IDLI </strong>
                </a>
            </div>

            <div id="nav-button">
                <button>My Wallet</button>
            </div>
        </nav>
    )
}

export default Nav